import * as History from './history-3.3.0/modules/index';
window.History = History;

function camelCase(name) {
  return name.charAt(0).toUpperCase() +
    name.slice(1).replace(/-(\w)/g, (m, n) => {
      return n.toUpperCase();
    });
}


const req = require.context('./ecology9', true, /^\.\/[^_][\w-]+\/small\.js?$/);

req.keys().forEach(mod => {
  // const v = req(mod);
  const match = mod.match(/^\.\/([^_][\w-]+)\/small\.js?$/);
  if (match && match[1]) {
    try {
      // console.log(v.default);
      const name = camelCase(match[1]);
      // v.default = util.tryCatch(React, MyErrorHandler, {error: "前端组件库异常，请联系管理员!"})(v.default);
      exports[name] = req(mod).default;
    } catch (e) {
      console.log(e);
    }
  }
});



