import { Tooltip } from 'antd';
import isString from 'lodash/isString'
import "./index.less";

const renderTitle = (title = '') => {
  if(isString(title)){
    return <div dangerouslySetInnerHTML={{__html: title}}></div>
  }
  return title
}
class main extends React.Component {
  render () {
    return (
      <span className="wea-helpful-tip" style={this.props.style}>
        {this.props.isCenter ?
          <Tooltip
            {...this.props}
            ref="weatip"
            overlayClassName = 'wea-helpful-tip-wrapper wea-helpful-tip-wrapper-center'
            placement = 'bottomLeft'
            arrowPointAtCenter
            overlayStyle = {{width : this.props.width || 250}}
            title = {renderTitle(this.props.title)}
          >
            <em className="icon-coms-Explain"></em>
          </Tooltip> :
          <Tooltip
            {...this.props}
            ref="weatip"
            overlayClassName = 'wea-helpful-tip-wrapper'
            placement = 'bottomLeft'
            arrowPointAtCenter
            overlayStyle = {{width : this.props.width || 250}}
            title = {renderTitle(this.props.title)}
          >
            <em className="icon-coms-Explain"></em>
          </Tooltip>
        }
      </span>
    
    )
  }
}

export default main


