import loadjs from "loadjs";
import getFrameParams from "./getUrlParams";
import * as caUtil from "@callApi";

const callApi = caUtil.callApi;

export const checkSSO = (path, checkCustom = true) => {
  return new Promise((rs, rj) => {
    if (!window.ssoChecked && !window.isAdminIndex) {
      if (/.*em_auth_code.*/.test(window.location.href)) {
        if (window.ssoChecking) {
          let interval = setInterval(() => {
            if (window.ssoChecking) {
              return;
            } else {
              clearInterval(interval);
              rs();
            }
          }, 500);
        } else {
          let params = getFrameParams();
          window.ssoChecking = true;
          callApi("/api/ec/dev/app/test", "GET", {
            em_auth_code: params["em_auth_code"]
          }).then(res => {
            window.ssoChecked = true;
            window.ssoChecking = false;
            if (!res.status) {
              window.location.href = "/wui/index.html";
              rs();
            }
          });
        }
      } else {
        if (checkCustom) {
          if (!window.e9ecComConfig) {
            loadjs.isDefined("customSSOConfig")
              ? loadjs.ready(
                  "customSSOConfig",
                  () => {
                    checkCustomSSO(path).then(() => {
                      rs();
                    });
                  },
                  () => {
                    rs();
                  }
                )
              : loadjs(
                  "/spa/coms/ssoConfig/config.js",
                  "customSSOConfig",
                  () =>
                    checkCustomSSO(path).then(() => {
                      rs();
                    }),
                  () => {
                    rs();
                  }
                );
          } else {
            checkCustomSSO(path).then(() => {
              rs();
            });
          }
        } else {
          rs();
        }
      }
    } else {
      rs();
    }
  });
};
export const checkCustomSSO = path => {
  return new Promise((rs, rj) => {
    if (!window.ssoCustomChecked && !window.isAdminIndex) {
      window.ssoCustomChecked = true;
      if (window.customSSOConfig) {
        if (window.customSSOConfig.inUse) {
          let params = getFrameParams();
          let checkSessionParams = {};
          if (window.customSSOConfig.sessionCheckApiParams) {
            let sessionParams = window.customSSOConfig.sessionCheckApiParams.split(
              ","
            );
            sessionParams.forEach(element => {
              checkSessionParams[element] = params[element];
            });
          }
          callApi(
            window.customSSOConfig.sessionCheckApi,
            window.customSSOConfig.sessionCheckApiType || "GET",
            checkSessionParams
          ).then(res => {
            if (res.status && !res.hasSession) {
              let service =
                window.location.protocol +
                "//" +
                window.location.host +
                "/wui/cas-entrance.jsp?path=" +
                encodeURIComponent(path || window.location.href);
              let paramsMark = "?";
              if (window.customSSOConfig.serverLoginUrl.indexOf("?") !== -1) {
                paramsMark = "&";
              }
              window.location.href = `${
                window.customSSOConfig.serverLoginUrl
              }${paramsMark}service=${encodeURIComponent(service)}`;
              rs();
            } else {
              rs();
            }
          });
        } else {
          rs();
        }
      } else {
        rs();
      }
    } else {
      rs();
    }
  });
};

export const ssoLogout = logoutRes => {
  if (!window.e9ecComConfig) {
    loadjs.isDefined("customSSOConfig")
      ? getLogoutUrl(logoutRes)
      : loadjs(
          "/spa/coms/ssoConfig/config.js",
          "customSSOConfig",
          () => getLogoutUrl(logoutRes),
          () => {
            if (logoutRes.casLogoutUrl) {
              let split = "?";
              if (logoutRes.casLogoutUrl.indexOf("?") > -1) {
                split = "&";
              }
              window.location.href =
                logoutRes.casLogoutUrl +
                split +
                "e9timestamp" +
                new Date().getTime();
            } else {
              window.location.href = "/wui/index.html";
            }
          }
        );
  } else {
    getLogoutUrl(logoutRes);
  }
};

const getLogoutUrl = (logoutRes, isEngine) => {
  if (logoutRes.status == true) {
    if (window.customSSOConfig && window.customSSOConfig.inUse) {
      if (window.customSSOConfig.serverLogoutUrl) {
        if (window.customSSOConfig.serverLogoutAjax) {
          // TODO
        } else {
          window.location.href = window.customSSOConfig.serverLogoutUrl;
        }
      } else {
        window.location.href = "/wui/index.html";
      }
    } else {
      if (logoutRes.casLogoutUrl) {
        let split = "?";
        if (logoutRes.casLogoutUrl.indexOf("?") > -1) {
          split = "&";
        }
        window.location.href =
          logoutRes.casLogoutUrl + split + "e9timestamp" + new Date().getTime();
      } else {
        window.location.href = "/wui/index.html";
      }
    }
  }
};

window.onLogoutCallback = function() {
  window.localStorage["e9_locale"] = null;
  window.e9_locale = null;

  if (window.emClient) {
    emClient.logout();
  }
  if (window.onLogoutCustom && typeof window.onLogoutCustom === "function") {
    // TODO
  }
};



