import React from "react";
import { Checkbox, Switch } from "antd";
import WeaHelpfulTip from "../wea-helpful-tip";
import WeaLocaleProvider from "../wea-locale-provider";

const { formatMultiLang } = WeaLocaleProvider;

class main extends React.Component {
  static defaultProps = {
    display: "normal"
  };
  constructor(props) {
    super(props);
    this.state = {
      value: props.value ? props.value : "0"
    };
  }
  componentWillReceiveProps(nextProps, nextState) {
    if ("value" in nextProps) {
      this.setState({ value: nextProps.value });
    }
  }
  doChange(e) {
    let value;
    if (this.props.display == "switch") {
      value = e ? "1" : "0";
    } else {
      value = e.target.checked ? "1" : "0";
    }
    this.setState({ value });
    this.props.onChange && this.props.onChange(value);
  }

  render() {
    const {
      viewAttr,
      fieldName,
      display,
      content,
      disabled,
      helpfulTip,
      helpfulTipProps
    } = this.props;
    const { value } = this.state;
    if (display == "switch") {
      return (
        <div
          className="wea-switch"
          style={{
            width: 40,
            height: 25,
            ...this.props.style,
            position: "relative"
          }}
        >
          <Switch
            disabled={viewAttr == 1 || disabled}
            checked={value == "1"}
            onChange={this.doChange.bind(this)}
          />
          <input type="hidden" id={fieldName} name={fieldName} value={value} />
          {viewAttr == "3" && value !== "1" && value !== "0" && (
            <span className="wea-required-e9">
              <img src="/images/BacoError_wev9.png" align="middle" />
            </span>
          )}
          {helpfulTip && (
            <WeaHelpfulTip
              style={{ position: "absolute", right: "-25px", top: "0px" }}
              title={helpfulTip}
              {...helpfulTipProps}
            />
          )}
        </div>
      );
    }
    return (
      <div className="wea-checkbox" style={this.props.style}>
        <Checkbox
          disabled={viewAttr == 1 || disabled}
          checked={value == "1"}
          indeterminate={value == "2"}
          onChange={this.doChange.bind(this)}
          className="content"
        >
          {content ? formatMultiLang(content) : null}
        </Checkbox>
        <input type="hidden" id={fieldName} name={fieldName} value={value} />
        {viewAttr == "3" && value !== "1" && value !== "0" && (
          <span className="wea-required-e9">
            <img src="/images/BacoError_wev9.png" align="middle" />
          </span>
        )}
        {helpfulTip && (
          <WeaHelpfulTip
            style={{ position: "absolute", right: "-25px", top: "0px" }}
            title={helpfulTip}
            {...helpfulTipProps}
          />
        )}
      </div>
    );
  }
}

export default main;



